* {
  box-sizing: border-box;
}

.cat-container {
  width: 320px;
  height: 509px;
  margin: 0 40px;
}

.container-footer-text {
  font-family: 'Trebuchet MS';
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 15px;
  text-align: center;
  color: #ffffff;
  margin-top: 14px;
}

.container-footer-link {
  text-decoration: none;
  color: #1698d9;
}

.container-footer-link span {
  border-bottom: 1px #1698d9 dashed;
}

.Cat {
  width: 320px;
  height: 480px;
  background-image: url('./img/Back-Cat.png');
  background-repeat: no-repeat;
  position: relative;
}

.head {
  width: calc(100% - 43px);
  margin-left: 43px;
  height: 43px;
  border: 4px solid #1698d9;
  border-radius: 0px 12px 0px 0px;
  border-bottom: none;
  border-left: none;
}

.select-head {
  border-color: #d91667;
}

.head-text {
  font-family: 'Trebuchet MS';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #666666;
  position: absolute;
  top: 20px;
  right: 64px;
}

.select-head-text {
  font-family: 'Trebuchet MS';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #e62e7a;
  left: 51px;
  text-align: left;
}

.head::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 43px;
  height: 43px;
  background: linear-gradient(
    -45deg,
    #f2f2f2,
    #f2f2f2 28.5px,
    #1698d9 0,
    #1698d9 32px,
    transparent 0
  );
}

.select-head::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 43px;
  height: 43px;
  background: linear-gradient(
    -45deg,
    #f2f2f2,
    #f2f2f2 28.5px,
    #d91667 0,
    #d91667 32px,
    transparent 0
  );
}

.main {
  height: 437px;
  width: 100%;
  border: 4px solid #1698d9;
  border-top: none;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
}

.select-main {
  border-color: #d91667;
}

.main-h {
  position: absolute;
  right: 64px;
  top: 45px;
  font-family: 'Trebuchet MS';
  font-style: normal;
  font-weight: 700;
  font-size: 48px;
  line-height: 56px;
  color: #000000;
}

.main-h4 {
  position: absolute;
  left: 51px;
  top: 103px;
  font-family: 'Trebuchet MS';
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 28px;
  color: #000000;
  text-align: left;
}

.main-text {
  text-align: left;
  margin-top: 15px;
  height: 32px;
  font-family: 'Trebuchet MS';
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  color: #666666;
}

.circle {
  position: absolute;
  right: 16px;
  bottom: 16px;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: #1698d9;
  display: flex;
  align-items: center;
  justify-content: center;
}

.select-circle {
  background: #d91667;
}

.circle-text {
  font-family: 'Trebuchet MS';
  font-style: normal;
  font-weight: 400;
  font-size: 42px;
  line-height: 22px;
  text-align: center;
  color: #ffffff;
  padding-top: 20px;
}

.small-gr {
  margin-top: 5px;
  font-size: 21px;
}

.head,
head::after,
.main,
.circle {
  transition: all 0.1s linear;
}

.Cat:hover .head,
.Cat:hover .main {
  border-color: #2ea8e6;
}

.Cat:hover .head::after {
  background: linear-gradient(
    -45deg,
    #f2f2f2,
    #f2f2f2 28.5px,
    #2ea8e6 0,
    #2ea8e6 32px,
    transparent 0
  );
}

.Cat:hover .circle {
  background: #2ea8e6;
}

.Cat:hover .select-head,
.Cat:hover .select-main {
  border-color: #e52e7a;
}

.Cat:hover .select-head::after {
  background: linear-gradient(
    -45deg,
    #f2f2f2,
    #f2f2f2 28.5px,
    #e52e7a 0,
    #e52e7a 32px,
    transparent 0
  );
}

.Cat:hover .select-circle {
  background: #e52e7a;
}

.grey,
.grey::after {
  color: #b3b3b3;
  border-color: #b3b3b3;
}

.grey::after {
  background: linear-gradient(
    -45deg,
    #f2f2f2,
    #f2f2f2 28.5px,
    #b3b3b3 0,
    #b3b3b3 32px,
    transparent 0
  );
}

.Cat-grey {
  width: 320px;
  height: 480px;
  background-image: url('./img/Back-Cat-Disable.png');
  background-repeat: no-repeat;
  position: relative;
}

.circle-grey {
  background-color: #b3b3b3;
}
