.App {
  min-height: 100vh;
  text-align: center;
  background-image: url(./img/Background.svg);
  position: relative;
  padding: 36px;
}

.qq {
  width: 0;
  height: 0;
  border-top: 50px solid transparent;
  border-right: 50px solid red;
}

.App-h1 {
  margin-bottom: 25px;
  font-family: 'Exo 2.0';
  font-style: normal;
  font-weight: 250;
  font-size: 36px;
  line-height: 43px;
  text-align: center;

  color: #ffffff;

  text-shadow: 0px 1px 1px #000000;
}

.Cats {
  display: flex;
  justify-content: center;
  margin: auto;
}
